export default{
	data(){
		return{
			list:[],
			schoolShow:true,
			serachStr:"",
			schoolId:null,
		}
	},
	methods:{
		addschool(){
			//搜索无学校数据，前去添加学校
			uni.navigateTo({
				url:"addschool"
			})
		},
		goBack(){
			uni.navigateBack({
			})
		},
		getSchoolList(){
			uni.showLoading({
				icon:"loading",
				title:"正在搜索"
			})
			this.$http.post("puSchool/list",{name:this.serachStr}).then(res=>{
				if(res.code==100){
					uni.hideLoading()
					if(res.info.length==0){
						this.schoolShow=false
					}else{
						this.schoolShow=true
						this.list=res.info
						if(res.info.length==1){
							this.schoolId=res.info[0].id
							this.schoolName=res.info[0].name
						}
						// var data={
						// 	id:2,
						// 	name:"测试",
							
						// }
						// this.list.push(data)
					}
				}else{
					uni.showToast({
						icon:"none",
						title:res.msg
					})
				}
			})
		},
		strChange(){
			// console.log('输入框变化')
			// console.log(this.serachStr)
		},
		checkSchool(item){
			// 选择学校
			this.schoolId=item.id
			this.schoolName=item.name
		},
		nextStep(){
			// 下一步
			if(this.schoolId){
				let data={
					schoolId:this.schoolId,
					schoolName:this.schoolName
				}
				data=JSON.stringify(data)
				uni.setStorageSync("addchildinfo",data)
				uni.navigateTo({
					url:"addchildmsg"
				})
				// components/information/addchildmsg
			}else{
				uni.showToast({
					icon:"none",
					title:"请选择学校！"
				})
			}
		},
	}
}
